$orange : #F7941E;
$white : #F1F1F2;
$black: #231F20;

.gifts__container__block:hover {

    background-color: #ffffff;
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05); }

.blogs__block-2:hover {
    box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.05); }


.header-navig__menu a::before,
.footer__navig a::before {
    content: '';

    position: absolute;
    right: 0;
    bottom: 0;

    width: 0;
    height: 2px;
    background-color: $black;

    transition: .3s; }



.header-navig__menu a::after,
.footer__navig a::after {
    content: '';

    position: absolute;
    left: 0;
    top: 0;

    width: 0;
    height: 2px;
    background-color: $black;

    transition: .3s; }





.footer__navig a:hover:after,
.header-navig__menu a:hover:after {
    width: 100%;
    right: 0; }

.footer__navig a:hover:before,
.header-navig__menu a:hover:before {
    width: 100%;
    left: 0; }




.header-navig__button a::before {
    content: "";
    position: absolute;
    background: $black;
    bottom: 0;
    left: 0;
    right: 0;
    top: 100%;
    z-index: -1;
    transition: top 0.2s ease-in; }

.header-navig__button a:hover {
    color:#fff {}
    border: 2px solid $black; }



.header-navig__button a:hover:before {
    top: 0; }


.gifts__container__block a::before,
.blogs__block-2 a::before,
.blogs__block-1__info a::before {
    content: "";
    position: absolute;
    background: $black;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    transform: scale(0);
    transition: .2s; }
.blogs__block-1__info a::before {
    background: #ffffff!important; }

.logo-dis-anim {
    width: 0; }


.gifts__container__block a:hover:before,
.blogs__block-2 a:hover:before,
.blogs__block-1__info a:hover:before {
    transform: scale(1);
    transition: .2s; }

.footer__social a img:hover {
    transform: scale(1.2); }
